import { Component, OnInit } from '@angular/core';
import { Http } from '@angular/http';
import { connectableObservableDescriptor } from 'rxjs/internal/observable/ConnectableObservable';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
  isAdded: boolean = false;
  issample:boolean=false;
  confirmationString:string = "please register with unique ID! Refresh the page and try again";
  confirmationmationString:string = "succesfully registered";
  
  constructor(private http: Http,private router: Router, private route: ActivatedRoute) { }
  
  ngOnInit() {
  }
  productObj:object = {};
  register = function(product) {
    this.productObj = {
      "firstname": product.firstname,
      "middlename": product.middlename,
      "lastname": product.lastname,
      "email": product.email,
      "mobilenumber": product.mobile,
      "password": product.password,
      "confirmpassword": product.confirmpassword,
      "id":product.id
    }
    this.http.get("http://localhost:555/login").subscribe(
      (res: Response) => {
        this.products = res.json();
    var i;
    for(i=0;i<this.products.length;i++)
    {
      
    console.log(this.productObj)
    if(this.productObj.password==this.productObj.confirmpassword && this.productObj.id!=this.products[i].password )
    {
      console.log("success");
    this.http.post("http://localhost:555/login", this.productObj).subscribe((res:Response) => {
      this.issample=true;
    })
    
  }
  else{
    this.isAdded = true;
  }
}
})
  }

  back(){
    console.log("home")
    this.router.navigate(['/home']);
  }
}
