import { Component, OnInit } from '@angular/core';

import 'rxjs/add/operator/toPromise';
import { Http } from '@angular/http';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  constructor(private http: Http,private router: Router, private route: ActivatedRoute) { }
  id:number;
  public headers = new Headers({ 'Content-Type': 'application/json'});

  products = [];
  fetchData = function() {
    this.http.get("http://localhost:5555/users").subscribe(
      (res: Response) => {
        this.products = res.json();
      
      }
    )
  }

  deleteProduct = function(id) {
    if(confirm("Are you sure?")) {
      const url = `${"http://localhost:5555/users"}/${id}`;
      return this.http.delete(url, {headers: this.headers}).toPromise()
        .then(() => {
        this.fetchData();
        })
    }
  }


  
  confirmationString:string = "New product has been added please refresh!";
  isAdded: boolean = false;
  productObj:object = {};

  addNewProduct = function(product) {
    this.productObj = {
      "name": product.name,
      "color": product.color,
      "email": product.email,
      "class": product.class,
      "year": product.year,
      "city": product.city,
      "counrty":product.counrty,
      "id":product.id

    }
    console.log(this.productObj)
    this.http.post("http://localhost:5555/users", this.productObj).subscribe((res:Response) => {
      this.isAdded = true;
    })
  }

  logout(){
    this.router.navigate(["/"]);
  }
  ngOnInit() {
    this.fetchData();
  }
}
